import React from 'react';

export default props => (
  <svg
    width='25px'
    height='25px'
    version='1.1'
    xmlns='http://www.w3.org/2000/svg'
    xmlnsXlink='http://www.w3.org/1999/xlink'
    xmlSpace='preserve'
    x='0px'
    y='0px'
    enableBackground='new 0 0 1000 1000'
    { ...props }
  />
);


